cart-positions {
    .final {
        background-color: #d9edf7;
        line-height: 45px;
        font-weight: bold;
        box-shadow: inset 0 0 4px 2px rgba(255, 255, 255, 1);
    }
    .final-price {
        margin-left: 95px;
    }
    .even {
        background-color: #efefef;
        box-shadow: inset 0 0 4px 2px rgba(255, 255, 255, 1);
    }
    .position-item {
        padding: 10px 0;
    }
    @include media-breakpoint-down(sm) {
        .position-first {
            margin-bottom: 5px;
        }
    }
    @include media-breakpoint-up(sm) {
        .final-name {
            margin-left: 55px;
        }

    }

}

